import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import Inicio from './componentes/Inicio.jsx'

function App() {
  return (
    <Router>
        <Switch>
          <Route 
            exact path="/"
            component={Inicio}          
          />
        </Switch>
    </Router>
  );
}

export default App;
